* {
  box-sizing: border-box;
}

.editor-wrapper {
  max-width: 600px;
  margin: 1.125rem auto;
}

.maximized {
  width: 96%;
  height: 100vh;
  margin: auto;
}

.hide {
  display: none;
}

.maximized #editor {
  height: 95vh;
}

#editor{
  min-height: 200px;
  width: 100%;
}

.preview-wrapper {
  max-width: 800px;
  margin: 1.25rem auto;
  min-height: 200px;
  overflow-wrap: break-word;
}

#toolbar {
  display: flex;
  align-items: center;
  background-color: #4aa3a3;
  padding: 0.3rem;
  border: 1px solid #1d2f2f;
  box-shadow: 1px 1px 10px 2px #3a5f5f;
  font-family: Russo One;
  font-size: 1rem;
  font-weight: bold;
  color: black;
}

#toolbar i {
  width: 25px;
  margin-left: 5px;
}

.fa-free-code-camp {
  margin-right: 3px;
}

#toolbar .fa-maximize, .fa-down-left-and-up-right-to-center {
  margin-left: auto !important;
}

#toolbar .fa-maximize:hover {
  color: #57e2b1;
  cursor: pointer;
}

#toolbar .fa-down-left-and-up-right-to-center:hover {
  color: #57e2b1;
  cursor: pointer;
}

#preview {
  padding: 0 1rem;
  background-color: #c0d8d8;
  border: 1px solid #1d2f2f;
  box-shadow: 1px 1px 10px 2px #3a5f5f;
}

#preview h1 {
  border-bottom: 2px solid #224b4b;
}

#preview h2 {
  border-bottom: 1px solid #224b4b;
}

#preview pre {
  display: block;
  overflow: auto;
  background: white;
  padding: 5px;
  line-height: 1.2;
}

#preview code {
  background: white;
  font-size: 0.875rem;
  font-weight: bold;
}

#preview blockquote {
    border-left: 3px solid #224b4b;
    color: #224b4b;
    padding-left: 5px;
    margin-left: 25px;
}

#preview table {
  border-collapse: collapse;
}

#preview img {
  display: block;
  max-width: 90%;
  margin: 2rem auto;
}
